nst routerParams = router.getRouteParams<{ tagId: string }>();\n    backofficeUseCase.setBreadcrumb([\"Tags\", \"Tag Detail\"]);\n    const tag = ref<ITags>();\n    const orgInfo = ref<Array<IOrganization>>([]);\n    const organizationsDataIsReady = ref(false);\n\n    onBeforeMount(() => {\n        getOrganizations();\n    })\n    function formatDate(date?: Date) {\n        if(date) {\n            const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n            const formattedDate = formatter.format(new Date(date));\n            return formattedDate;\n        }\n    }\n    function getOrganizations() {\n        executor.execute(async () => {\n            tag.value = await backofficeUseCase.getTag(routerParams.tagId);\n            orgInfo.value = await backofficeUseCase.getOrganizationsByTagId(routerParams.tagId);\n            organizationsDataIsReady.value = true;\n        }, { loading: true });\n    }\n\n    function openUpdateTagDialog() {\n        const closeCb = (reason: \"close\" | \"outclick\" | \"component\") => {\n            if(reason == \"component\") {\n                getOrganizations();\n            }\n        }\n        dialog.showDialog(\n            defineAsyncComponent(() => import(`../../components/dialogs/backoffice/addOrUpdateTag.vue`)),\n            { closable: true, title: \"Update Tag\", width: \"542px\", maxHeight: \"379px\", closeCb }, {tag: tag.value}\n        );\n    }\n</script>\n\n<template>\n    <div class=\"manage-tag-div\">\n        <button class=\"manage-btn\" @click=\"openUpdateTagDialog()\"><i class=\"mdi mdi-cog\" style=\"font-size: 20px; margin-right: 4px\"></i><span>UPDATE TAG</span></button>\n    </div>  \n    <div  style=\"display: flex; margin-bottom: 32px;\">\n        <div class=\"tag-info-type\">\n            <div class=\"tag-info-type-name\">Name</div>\n            <div class=\"tag-info\">{{ tag?.name }}</div>\n        </div>\n        <div class=\"tag-info-type\">\n            <div class=\"tag-info-type-name\">Organization Count</div>\n            <div class=\"tag-info\">{{ orgInfo.length }}</div>\n        </div>\n        <div class=\"tag-info-type\">\n            <div class=\"tag-info-type-name\">Description</div>\n            <div class=\"tag-info\">{{ tag?.description }}</div>\n        </div>\n        <div class=\"tag-info-type\">\n            <div class=\"tag-info-type-name\">ID</div>\n            <div class=\"tag-info\" style=\"font-size: 20px;\">{{ tag?.ID }}</div>\n        </div>\n    </div>\n    <div  style=\"display: flex; margin-bottom: 32px;\">\n        <div class=\"tag-info-type\">\n            <div class=\"tag-info-type-name\">Date Created</div>\n            <div class=\"tag-info\">{{ formatDate(tag?.createDate) }}</div>\n        </div>\n        <div class=\"tag-info-type\">\n            <div class=\"tag-info-type-name\">Creted By</div>\n            <div class=\"tag-info\" style=\"font-size: 20px;\">{{ tag?.createdBy }}</div>\n        </div>\n        <div class=\"tag-info-type\">\n            <div class=\"tag-info-type-name\">Date Updated</div>\n            <div class=\"tag-info\">{{ formatDate(tag?.updateDate) }}</div>\n        </div>\n        <div class=\"tag-info-type\">\n            <div class=\"tag-info-type-name\">Updated By</div>\n            <div class=\"tag-info\" style=\"font-size: 20px;\">{{ tag?.updatedBy }}</div>\n        </div>\n    </div>\n    <div v-if=\"organizationsDataIsReady\" style=\"margin-bottom: 32px;\">\n        <OrganizationList :organizations=\"orgInfo\" :tagID = \"tag?.ID\" @refreshTable=\"getOrganizations\"></OrganizationList>\n    </div>\n</template>\n<style scoped>\n    .manage-tag-div {\n        width: 85%; \n        display: flex; \n        justify-content: end;\n        position: absolute;\n        top: 80px;\n        right: 36px;\n    }\n    .manage-btn {\n        border: 1px solid #151233;\n        border-radius: 4px;\n        padding: 1.5px 16px;\n        font-size: 15px;\n        color: #151233;\n        display: flex;\n        align-items: center;\n        font-weight: 500;\n    }\n    .tag-info-type {\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        background: #FFFFFF;\n        margin-right: 37px;\n        padding: 24px 24px 18px;\n        display: flex;\n        flex-direction: column;\n        flex: 1;\n    }\n    .tag-info-type:last-child {\n        margin-right: 0;\n    }\n    .tag-info-type-name {\n        font-weight: 500;\n        font-size: 20px;\n        line-height: 23px;\n        color: #757575;\n        margin-bottom: 20px;\n        min-width: 100px;\n        flex: 1;\n    }\n    .tag-info {\n        font-weight: 500;\n        font-size: 28px;\n        line-height: 33px;\n        color: #151233; \n    }\n    /* @media (min-width: 2400px) {\n        .tag-info-type {\n            flex: 0 0 auto;\n            width: calc(100% / 7);\n        }\n    }\n\n    @media (max-width: 2400px) {\n        .tag-info-type {\n            flex: 0 0 auto;\n            width: calc(100% / 6);\n        }\n    }\n\n    @media (max-width: 2150px) {\n        .tag-info-type {\n            flex: 0 0 auto;\n            width: calc(100% / 5);\n        }\n    }\n\n    @media (max-width: 1820px) {\n        .tag-info-type {\n            flex: 0 0 auto;\n            width: calc(100% / 4);\n        }\n    }\n\n    @media (max-width: 1470px) {\n        .tag-info-type {\n            flex: 1 0 auto;\n            width: calc(100% / 3);\n        }\n    }\n\n    @media (max-width: 1250px) {\n        .tag-info-type {\n            flex: 1 0 auto;\n            width: calc(100% / 2);\n        }\n    }\n\n    @media (max-width: 950px) {\n        .tag-info-type {\n            flex: 0 0 auto;\n            width: 100%;\n        }\n    } */\n</style>"],"sourceRoot":""}]);
